<template>
  <div class="sn-page gscp-style" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/gscp_icon_background.png')`}">
    <!-- 左上角标题 -->
    <img class="head" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gscp_icon_title.png'" alt="">

    <!-- 数据 -->
    <div class="sn-f-s-s data-box" v-if="dataList.length">
      <!-- 左侧点击按钮 -->
      <div class="left-btn">
        <ul class="sn-f-s-c">
          <li class="sn-f-s-c" v-for="(obj, i) in dataList" :key="i" @click="funChange(i)"
            :class="{'active-li': i === nowIndex}">
            <img v-show="i === nowIndex" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gscp_icon_markxz.png'" alt="">
            <img v-show="i !== nowIndex" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gscp_icon_mark.png'" alt="">
            <p>{{ obj.title }}</p>
          </li>
        </ul>
      </div>

      <!-- 右侧数据展示 -->
      <div class="right-data">
        <ul class="sn-f-s-c general">
          <li class="sn-f-c-c btn-name" v-for="(item, index) in nowObj.label" :key="index">
            {{ item.title }}
          </li>
        </ul>
        <!-- 背景图 -->
        <img :src="ALI_CLOUD_DOMAIN_STATIC + nowObj.background" alt="">
        <!-- 数据 -->
        <div class="list">
          <!-- 标题 -->
          <div class="title">{{ nowObj.title }}</div>
          <!-- 列表 -->
          <ul>
            <li v-for="(obj, i) in nowObj.caption" :key="i">
              <p v-show="i < 2" class="linear"></p>
              {{ obj.title }}
            </li>
          </ul>
          <!-- <div class="general-right sn-f-col-s-c">
            <div class="sn-f-c-c btn-name" v-for="(item, index) in nowObj.general" :key="index">
              {{ item }}
            </div>
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import * as constants from '../../../common/utils/constants';

export default {
  name: 'gscp',

  props: {
    resData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径

      nowIndex: 0, // 右侧显示数据下标
      dataList: [ // 数据
        // {
        //   'title': '互联网医院',
        //   'background': '/hos/dashboard/images/description/gscp_background_hlwyy.png',
        //   'label': [{
        //     'title': '智慧门诊',
        //     'sort': '0',
        //     'remark': ''
        //   }, {
        //     'title': '智慧住院',
        //     'sort': '1',
        //     'remark': ''
        //   }, {
        //     'title': '统一支付对账',
        //     'sort': '2',
        //     'remark': ''
        //   }],
        //   'caption': [{
        //     'title': '构建覆盖诊前、诊中、诊后的线上线下一体化医疗服务模式',
        //     'sort': '0',
        //     'remark': ''
        //   }, {
        //     'title': '在线开展部分常见病、慢性病复诊，拓展了医疗服务半径',
        //     'sort': '1',
        //     'remark': ''
        //   }, {
        //     'title': '打造符合多场景互联网诊疗业务的同时，实现真正意义上的线下业务线上化',
        //     'sort': '2',
        //     'remark': ''
        //   }],
        //   'sort': '0',
        //   'remark': ''
        // }
      ]
    };
  },

  computed: {
    nowObj() {
      return this.dataList.length ? this.dataList[this.nowIndex || 0] : [];
    }
  },

  created() {
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-04-14 09:18:59 Ywr
    * 数据初始化
    */
    funLoadData() {
      const res = this.resData;
      let list = res.info;
      list = list.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });
      for (const obj of list) {
        obj.label = obj.label.sort((a, b) => {
          return (+a.sort) - (+b.sort);
        });
        obj.caption = obj.caption.sort((a, b) => {
          return (+a.sort) - (+b.sort);
        });
      }

      this.dataList = list;
    },

    /*
    * 2023-04-03 15:34:43 Ywr
    * 切换数据
    */
    funChange(i) {
      this.nowIndex = i;
    }
  }
};
</script>

<style lang="scss">
.gscp-style {
  background-size: cover;
  background-repeat: no-repeat;

  // 左上角头部图片
  .head {
    margin-top: 70px;
    margin-left: 50px;
    width: 412px;
    height: 120px;
  }

  // 按钮 + 数据
  .data-box {
    margin-top: 107px;
    margin-left: 50px;

    // 左侧按钮
    .left-btn {
      margin-right: 55px;

      >ul {
        flex-wrap: wrap;
        max-width: 760px;

        >li {
          width: 361px;
          height: 152px;
          background: linear-gradient(136deg, $blue16 0%, $blue17 100%);
          font-size: 24px;
          line-height: 36px;
          color: $white;
          margin-bottom: 18px;
          margin-right: 18px;
          position: relative;

          // 外层边框
          &::before {
            position: absolute;
            top: 5px;
            left: 12px;
            content: '';
            display: block;
            width: 336px;
            height: 140px;
            opacity: 1;
            border: 1px solid;
            border-image: linear-gradient(336deg, $blue9, transparent) 1;
          }

          // 内层边框
          &::after {
            position: absolute;
            top: 10px;
            left: 22px;
            content: '';
            display: block;
            width: 316px;
            height: 131px;
            opacity: 1;
            border: 1px solid;
            border-image: linear-gradient(156deg, $blue9, transparent) 1;
          }

          img {
            width: 50px;
            height: 50px;
            margin-left: 50px;
            margin-right: 20px;
          }
        }

        .active-li {
          background: linear-gradient(134deg, $blue6 0%, $blue18 100%);
          font-weight: 600;
        }
      }
    }

    // 右侧展示数据
    .right-data {
      position: relative;
      width: 1017px;
      height: 486px;

      .general {
        position: absolute;
        left: 30px;
        top: 183px;

        .btn-name {
          padding: 0 17px;
          min-width: 162px;
          height: 40px;
          background: linear-gradient(180deg, $blue3 0%, $blue4 100%);
          border-radius: 24px;
          font-size: 16px;
          line-height: 24px;
          font-weight: 400;
          color: $white;
          margin-right: 10px;
        }
      }

      img {
        width: 100%;
        height: 100%;
      }

      // 数据列表
      .list {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 1017px;
        height: 243px;
        padding: 20px 30px;
        backdrop-filter: blur(20px);

        // 标题
        .title {
          font-size: 32px;
          line-height: 48px;
          font-weight: 600;
          color: $white;
          margin-bottom: 20px;
        }

        >ul {
          li {
            font-size: 18px;
            line-height: 27px;
            font-weight: 400;
            color: $white1;
            margin-bottom: 23px;
            position: relative;
          }

          .linear {
            bottom: -12px;
            left: 0;
            position: absolute;
            width: 468px;
            opacity: 1;
            border: 1px dashed $border1;
          }
        }

        // .general-right {
        //   position: absolute;
        //   height: 144px;
        //   width: 162px;
        //   right: 30px;
        //   top: 61px;

        //   .btn-name {
        //     width: 162px;
        //     height: 40px;
        //     background: linear-gradient(180deg, $blue3 0%, $blue4 100%);
        //     border-radius: 24px;
        //     font-size: 16px;
        //     line-height: 24px;
        //     font-weight: 400;
        //     color: $white;
        //     margin-bottom: 12px;
        //   }
        // }
      }
    }
  }
}
</style>
