<template>
  <div class="sn-page company-introduce" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/cyjg_icon_background.png')`}">
    <!-- 左上角标题 -->
    <img class="head" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/cyjg_icon_title.png'" alt="">

    <!-- 数据展示 -->
    <div class="data-box" v-if="dataList.length">
      <ul class="sn-f-s-c">
        <li class="sn-f-col-c-s" v-for="(obj, i) in dataList" :key="i"
          :class="[i%2 === 0 ? 'li-left' : 'li-right']">
          <!-- 圆圈 -->
          <div class="sn-f-c-c round">{{obj.value}}%</div>
          <!-- 名称 -->
          <div class="name">{{ obj.title }}</div>
          <!-- 简介 -->
          <div class="remark">{{ obj.sub }}</div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type="text/javascript">
import * as constants from '../../../common/utils/constants';

export default {
  name: 'cyjg',

  props: {
    resData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径
      dataList: []
    };
  },

  created() {
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-04-14 09:31:13 Ywr
    * 数据初始化
    */
    funLoadData() {
      const res = this.resData;
      res.info = res.info.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });
      this.dataList = res.info;
    }
  }
};
</script>

<style lang="scss">
.company-introduce {
  background-size: cover;
  background-repeat: no-repeat;

  // 左上角头部图片
  .head {
    margin-top: 70px;
    margin-left: 50px;
    width: 412px;
    height: 120px;
  }

  // 数据展示
  .data-box {
    margin-top: 79px;
    margin-left: 161px;

    ul {
      width: 1880px;
      flex-wrap: wrap;

      li {
        width: 668px;
        height: 251px;
        border: 1px solid;
        color: $white;
        position: relative;
        margin-right: 262px;
        margin-bottom: 40px;

        // 圆圈
        .round {
          width: 161px;
          height: 161px;
          border-radius: 50%;
          overflow: hidden;
          background: linear-gradient(134deg, $blue6 0%, $blue7 100%);
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 48px;
          line-height: 72px;
          font-weight: 600;
        }

        // 名称
        .name {
          font-size: 32px;
          line-height: 48px;
          font-weight: 600;
          margin-bottom: 25px;
        }

        // 简介
        .remark {
          line-height: 36px;
          font-size: 24px;
          font-weight: 400;
        }
      }

      // 左侧边框样式
      .li-left {
        background: linear-gradient(270deg, $black2 0%, $black3 100%);
        border-image: linear-gradient(to left, $blue10, transparent) 1;
        padding-left: 90px;
        border-left: 0;

        .round {
          right: -80.5px;
        }
      }

      // 右侧边框样式
      .li-right {
        background: linear-gradient(87deg, $black2 0%, $black3 100%);
        border-image: linear-gradient(to right, $blue10, transparent) 1;
        border-right: 0;
        padding-left: 173px;

        .round {
          left: -80.5px;
        }
      }
    }
  }
}
</style>
