<!--
 Page: introduce.vue [493793]
 From: 2023-04-03 10:30:22 Ywr
 Remark: 公司介绍-轮播
-->
<template>
  <div class="sn-page introduce" :style="{'background-image': showLayerBg ? `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/sy_icon_background.png')` : ''}">
    <!-- 轮播主页 -->
    <swiper ref="swiper" class="swiper" therehold='10' :autoplay="false" :pagination="false"
      :defaultSlide="currentPage" @transitionend="funOnSlideChange" @getLoadEnd="getLoadEnd"
      v-if="requestNum === 0 && showFunctionList.length > 1">
      <slide v-for="obj in showFunctionList.slice(1)" :key="obj.id" >
        <component :is="obj.id" :resData="obj.data" @getPreviewSrc="getPreviewSrc" @nextPrev="funChild"></component>
      </slide>
    </swiper>

    <!-- 底部切换按钮 -->
    <div class="switch-btn-box">
      <ul class="my-btn">
        <li class="sn-f-s-c" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/sub_background_function${(i - 1) === currentPage ? 'xz' : ''}.png')` }"
          v-for="(item, i) in showFunctionList" :key="i" @click="funClickBtn(i - 1)">
          <img :src="ALI_CLOUD_DOMAIN_STATIC + item.sub" alt="">
          <div>{{ item.title }}</div>
        </li>
      </ul>
    </div>

    <!-- 预览证书 -->
    <div class="sn-f-c-c preview-box" v-show="previewSrc" @click="previewSrc = ''">
      <img :src="previewSrc" alt="">
    </div>
  </div>
</template>
<script type="text/javascript">
// 常量
import * as constants from '../../common/utils/constants';
// 导入子组件
import gsjs from './components/gsjs.vue';
import tdjs from './components/tdjs.vue';
import fzlc from './components/fzlc.vue';
import gszl from './components/gszl.vue';
import cyjg from './components/cyjg.vue';
import gsry from './components/gsry.vue';
import gscp from './components/gscp.vue';

export default {
  components: {
    'gsjs': gsjs,
    'tdjs': tdjs,
    'fzlc': fzlc,
    'gszl': gszl,
    'cyjg': cyjg,
    'gsry': gsry,
    'gscp': gscp
  },

  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径
      previewSrc: '', // gsry组件中 预览图片
      Constants: constants, // 静态路径
      currentPage: null, // 当前下标项

      requestNum: 0, // 当前未请求完成 数量

      metaList: [ // 元数据
        {
          title: '首页',
          main: '',
          sub: '/hos/dashboard/images/description/sub_icon_sy.png',
          id: 'sy',
          suffix: '0',
          enable: '1',
          sort: '',
          remark: ''
        }
      ],

      showLayerBg: true // 首次加载展示默认图
    };
  },

  computed: {
    // 当前Swiper对象
    swiper() {
      return this.$refs.swiper;
    },

    // 展示的子页面
    showFunctionList() {
      return this.metaList.filter(el => {
        return el.enable === '1';
      });
    }
  },

  created() {
    // 初始化数据
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-04-13 16:26:26 Ywr
    * 初始化数据
    */
    funLoadData() {
      const params = {
        domain: this.ALI_CLOUD_DOMAIN_STATIC,
        url: `/hos/dashboard/caption/description/function.js?time=${this.$sn.format.date(this.$sn.utils.getCurrentTime(), 'yyyyMMddHHmmss')}`
      };
      this.$sn.utils.ordinaryRequest(params).then(res => {
        // 筛选开启的功能
        let List = res.function.filter(el => {
          return el.enable === '1';
        });
        // 排序
        List = List.sort((a, b) => {
          return +a.sort - (+b.sort);
        });

        // 元数据赋值
        this.metaList = List;

        // 轮播调取数据
        this.metaList.forEach(el => {
          if (el.id !== 'sy') {
            this.funGetComponentsData(el);
          }
        });

        this.currentPage = +this.$route.query.index;
      });
    },

    /*
    * 轮播反馈下标数据 (2023-03-30 13:42:45 Ywr)
    */
    funOnSlideChange(currentSlide) {
      this.currentPage = currentSlide;
    },

    /*
    * 2023-04-21 15:14:55 Ywr
    * 循环调取子组件js
    */
    funGetComponentsData(el) {
      // 如果含有请求数据
      if (el.suffix === '1') {
        // 默认不展示
        el.enable = '0';
        // 请求 +1
        this.requestNum++;
        const params = {
          domain: this.ALI_CLOUD_DOMAIN_STATIC,
          url: `/hos/dashboard/caption/description/${el.id}.js?time=${this.$sn.format.date(this.$sn.utils.getCurrentTime(), 'yyyyMMddHHmmss')}`
        };
        this.$sn.utils.ordinaryRequest(
          params,
          () => {},
          () => {
            // 请求 -1
            this.requestNum--;
          }
        ).then(res => {
          // 请求成功后 展示
          el.enable = '1';
          // 数据赋值
          el.data = res;
          // 请求 -1
          this.requestNum--;
        });
      }
    },

    /*
    * 2023-04-03 10:37:12 Ywr
    * 点击按钮切换
    */
    funClickBtn(i) {
      // 点击首页返回
      if (i === -1) {
        this.$router.back();
      }
      // 切换轮播
      else {
        this.swiper.slideTo(i);
      }
    },

    /*
    * 2023-06-12 16:35:53 Ywr
    * 发展历程组件函数-左滑右滑
    */
    funChild(type) {
      setTimeout(() => {
        type ? this.swiper.next() : this.swiper.previous();
      }, 4);
    },

    /*
    * 2023-04-06 14:41:16 Ywr
    * 获取子组件预览
    */
    getPreviewSrc(src) {
      this.previewSrc = src;
    },

    /*
    * 2023-05-19 11:15:05 Ywr
    * 轮播组件加载完成
    */
    getLoadEnd() {
      // 隐藏默认图
      this.showLayerBg = false;
    }
  },

  beforeDestroy() {
  }
};
</script>

<style lang="scss">
.introduce {
  background-size: cover;
  background-repeat: no-repeat;

  // 底部切换按钮
  .switch-btn-box {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 11;
    width: 100vw;
    text-align: center;
    border-top: 2px solid $border;
    backdrop-filter: blur(10px);

    .my-btn {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: 50px auto;
      width: fit-content;

      li {
        background-size: cover;
        background-repeat: no-repeat;
        min-width: 156px;
        width: fit-content;
        height: 61px;
        margin-right: 15px;
        padding-right: 20px;
        color: $white;
        font-size: 18px;
        line-height: 27px;
        font-weight: bold;

        img {
          width: 32px;
          height: fit-content;
          margin-left: 20px;
          margin-right: 15px;
        }
      }
    }
  }

  // 预览证书
  .preview-box {
    position: absolute;
    z-index: 12;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $mask;
    overflow-x: scroll;
    overflow-y: hidden;

    &::-webkit-scrollbar {
      display: none;
    }

    img {
      height: 900px;
      width: fit-content;
    }
  }
}
</style>
