<!--
 Page: history.vue [248654]
 From: 2023-04-03 13:53:50 Ywr
 Remark: 发展历程
-->
<template>
  <div class="sn-page history-style" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/fzlc_icon_background.png')`}">
    <!-- 左上角标题 -->
    <img class="head" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/fzlc_icon_title.png'" alt="">

    <!-- 发展历程 -->
    <div class="data-box can-scroll" ref="scroll"
      @mousedown="funStart" @mouseup="funEnd"
      @touchstart="funStart" @touchend="funEnd">
      <!-- 背景蓝条 -->
      <img :style="{'width': myUlWidth}" class="bg-img can-scroll" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/fzlc_background_year.png'" alt="">
      <!-- 发展历程 -->
      <ul ref="myUl" class="data-ul can-scroll" :style="{'padding-right': dataUlRight}">
        <li class="single can-scroll" :class="{'single-top': i%2 === 0, 'single-small': i < 5}" v-for="(obj, i) in historyList" :key="i">
          <!-- 年份 -->
          <img class="can-scroll" :src="ALI_CLOUD_DOMAIN_STATIC + obj.image" alt="">
          <!-- 线条 -->
          <div class="linear can-scroll"></div>
          <!-- 事件 -->
          <div class="msg can-scroll">
            <div class="remark can-scroll" v-for="(item, index) in obj.sub" :key="index">
              <div class="sub can-scroll" v-for="(end, endIndex) in item" :key="endIndex">{{ end }}</div>
            </div>
          </div>
        </li>
      </ul>
    </div>

  </div>
</template>
<script type="text/javascript">
import * as constants from '../../../common/utils/constants';

export default {
  name: 'fzlc',

  props: {
    resData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径
      dataUlRight: '10.41667vw', // 右侧内边距
      myUlWidth: '', // 背景图蓝条宽度
      ViewWidth: 1920, // 视口宽度
      startI: 0, // 起始位置
      who: null, // 左右滑记录动作事件？
      historyList: []
    };
  },

  created() {
    // 数据初始化
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-06-12 15:30:01 Ywr
    * 手指触摸事件
    */
    funStart(e) {
      this.who = null; // 记录动作事件？
      const nextPrevObj = {
        mousedown: () => {
          this.startI = e.offsetX;
        },
        touchstart: () => {
          this.startI = e.targetTouches?.[0]?.pageX;
        }
      };
      // 如果滚动到最左侧
      if (this.$refs.scroll.scrollLeft === 0) {
        nextPrevObj[e.type]();
        // 可能会触发上一页
        this.who = 'prev';
      }
      // 如果滚动到最右侧
      else if (Math.ceil(this.$refs.scroll.scrollLeft) + this.ViewWidth >= parseInt(+this.myUlWidth.slice(0, -2))) {
        nextPrevObj[e.type]();
        // 可能会触发下一页
        this.who = 'next';
      }
    },

    /*
    * 2023-06-12 15:30:16 Ywr
    * 手指离开事件
    */
    funEnd(e) {
      if (this.who) {
        const nextPrevObj = {
          mouseup: (this.startI - e.offsetX),
          touchend: (this.startI - e.changedTouches?.[0]?.pageX)
        };
        const end = nextPrevObj[e.type];
        // ------->
        if (end < 0 && this.who === 'prev') {
          this.$emit('nextPrev', false);
        }
        // <------
        else if (end > 0 && this.who === 'next') {
          this.$emit('nextPrev', true);
        }
      }
    },

    /*
    * 2023-04-14 09:14:45 Ywr
    * 数据初始化
    */
    funLoadData() {
      const res = this.resData;
      let list = res.info;
      // 排序
      list = list.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });

      // 内部文字是否 大于32 => 宽度744px
      list.forEach(el => {
        el.sub.forEach(strArr => {
          strArr.find(str => str.length > 28) && (el.width = 1010);
        });
      });
      this.historyList = list;

      // 最后一项文字 大于32 => 右内边距扩大
      this.ViewWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
      if (![...this.historyList].reverse()[0].width) {
        this.dataUlRight = 18.75 / 1920 * this.ViewWidth + 'vw';
      }
      else {
        this.dataUlRight = 38.75 / 1920 * this.ViewWidth + 'vw';
      }

      // 背景图宽度
      this.$nextTick(() => {
        setTimeout(() => {
          // 所有年份孩子
          const children = Array.prototype.slice.call(this.$refs.myUl.children);
          const arr = [];
          for (let i = 0; i < children.length - 1; i++) {
            arr.push(children[i].offsetWidth - 152 + 100);
          }

          // 当前视口宽度
          this.ViewWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
          for (let i = 0; i < children.length; i++) {
            children[i].style.width = (arr[i] / 1080) / 1920 * 100 * this.ViewWidth + 10 + 'vw';
          }
          // 背景蓝条宽度
          this.myUlWidth = this.$refs.myUl?.offsetWidth + 'px';
        }, 4);
      });
    }
  }
};
</script>

<style lang="scss">
.history-style {
  background-size: cover;
  background-repeat: no-repeat;

  ul {
    list-style: none;
  }

  // 左上角头部图片
  .head {
    margin-top: 101px;
    margin-left: 50px;
    width: 412px;
    height: 120px;
  }

  // 发展历程
  .data-box {
    margin-top: 73px;
    width: 100vw;
    height: 624px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    overflow-x: scroll;
    position: relative;

    &::-webkit-scrollbar {
      display: none;
    }

    .bg-img {
      width: 3185px;
      height: 175px;
      position: absolute;
      left: 0;
      top: 214px;
    }

    .data-ul {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 0 360px 0 57px;
      // padding: 0 200px 0 57px;
      margin-top: 282px;

      .single {
        transform: translate(0, -14px);
        // width: 255px;
        // width: 420px;
        // width: 550px;
        width: fit-content;
        height: 240px;
        position: relative;

        &::before {
          content: '';
          display: block;
          transform: translate(0, 115px);
          // position: absolute;
          // top: 96px;
          // left: 0;
          width: 14px;
          height: 14px;
          background: $blue5;
          box-shadow: 0 0 0 0 $shadow;
          border-radius: 50%;
          overflow: hidden;
          margin-right: 15px;
        }

        img {
          transform: translate(0, 0);
          width: 152px;
          height: 55px;
        }

        .linear {
          margin-left: 6px;
          width: 2px;
          height: 96px;
          background: linear-gradient(360deg, $blue5 0%, $white3 100%);
          position: relative;
          transform: translate(0, -55px);
        }

        .msg {
          margin-left: 22px;
          // width: 322px;
          // width: 342px;
          width: fit-content;
          max-height: 170px;
          overflow-y: scroll;
          font-size: 14px;
          line-height: 14px*1.8;
          font-weight: 400;
          color: $white1;
          transform: translate(0, -60px);
          // position: relative;
          // top: -4px;

          &::-webkit-scrollbar {
            display: none;
          }

          .sub {
            width: max-content;
          }
        }

        .remark:not(:last-child) {
          margin-bottom: 28px;
        }
      }

      .single-top {
        transform: translate(0, -132px);

        &::before {
          content: '';
          display: block;
          transform: translate(0, -196px);
          width: 14px;
          height: 14px;
          background: $blue5;
          box-shadow: 0 0 0 0 $shadow;
          border-radius: 50%;
          overflow: hidden;
          margin-right: 15px;
        }

        .linear {
          height: 256px;
          background: linear-gradient(180deg, $blue5 0%, $white3 100%);
          transform: translate(0, -256px);
        }

        .msg {
          transform: translate(0, -530px);
          // top: -467px;
          max-height: 200px;
        }
      }

      .single-small {
        // width: 420px;
        width: fit-content;
      }
    }
  }
}
</style>
