import * as constants from '../../../common/utils/constants';
// import Swagger from '@/common/utils/swagger.js';

export default {
  name: 'gsjs',

  props: {
    resData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      dataObj: [
        // {
        //   title: '服务宗旨',
        //   sub: '用户价值最大化',
        //   icon: '/hos/dashboard/images/description/gsjs_icon_fwzz.png'
        // },
        // {
        //   title: '企业理念',
        //   sub: '移动改变医疗，掌医守护健康',
