<template>
  <div class="sn-page strategy"  :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/gszl_icon_background.png')`}">
    <!-- 左上角标题 -->
    <img class="head" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gszl_icon_title.png'" alt=""/>

    <!-- 数据 -->
    <div class="sn-f-s-s data-box">
      <div class="single" v-for="(obj, i) in dataList" :key="i">
        <img :src="ALI_CLOUD_DOMAIN_STATIC + obj.background" alt="">
        <div class="msg-box">
          <div class="sn-f-c-c title">
            <img :src="ALI_CLOUD_DOMAIN_STATIC + obj.title" alt="">
          </div>
          <ul class="sn-f-s-c name">
            <li class="sn-f-b-c" v-for="(str, stri) in obj.label" :key="stri">
              {{ str.title }}
              <div v-if="stri !== obj.label.length-1" class="hr"></div>
            </li>
          </ul>
          <ul class="bottom-list">
            <li v-for="(item, i) in obj.caption" :key="i">
              <p v-show="i !== 0" class="linear"></p>
              {{ item.secretary }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import * as constants from '../../../common/utils/constants';

export default {
  name: 'gszl',

  props: {
    resData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径

      nowIndex: 0, // 右侧显示数据下标
      dataList: []
    };
  },

  computed: {
    nowObj() {
      return this.dataList.length ? this.dataList[this.nowIndex || 0] : [];
    }
  },

  created() {
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-04-14 09:18:59 Ywr
    * 数据初始化
    */
    funLoadData() {
      const res = this.resData;
      let list = res.info;
      list = list.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });
      for (const obj of list) {
        obj.label = obj.label.sort((a, b) => {
          return (+a.sort) - (+b.sort);
        });
        obj.caption = obj.caption.sort((a, b) => {
          return (+a.sort) - (+b.sort);
        });
      }

      this.dataList = list;
    },

    /*
    * 2023-04-03 15:34:43 Ywr
    * 切换数据
    */
    funChange(i) {
      this.nowIndex = i;
    }
  }
};
</script>

<style lang="scss">
.strategy {
  background-size: cover;
  background-repeat: no-repeat;

  // 左上角头部图片
  .head {
    margin-top: 70px;
    margin-left: 50px;
    width: 412px;
    height: 120px;
  }

  // 按钮 + 数据
  .data-box {
    margin: 0 auto;
    margin-top: 24px;
    width: fit-content;

    .single {
      position: relative;
      width: 572px;
      height: 690px;
      color: $white;

      &:not(:last-child) {
        // margin-right: 25px;
        margin-right: 8px;
      }

      img {
        position: relative;
        width: 572px;
        height: 341px;
        z-index: 1;
      }

      .msg-box {
        position: absolute;
        top: 178px;
        left: 27px;
        width: 518px;
        height: 494px;
        background: linear-gradient(180deg, #072552 0%, rgba(7, 41, 87, 0.84) 47%, rgba(5, 62, 114, 0) 100%);

        .title {
          position: relative;
          z-index: 3;
          padding-top: 66px;
          margin-bottom: 71px;

          img {
            height: 51px;
            width: fit-content;
          }
        }

        .name {
          margin-bottom: 30px;
          padding-left: 25px;

          li {
            width: 148px;
            height: 30px;
            font-size: 22px;
            font-weight: 600;

            .hr {
              margin-right: 30px;
              width: 0;
              height: 14px;
              border: 1px solid $white;
            }
          }
        }

        .bottom-list {
          padding-left: 25px;
          height: 228px;
          overflow-y: scroll;

          &::-webkit-scrollbar {
            display: none;
          }

          li {
            height: 70px;
            font-size: 22px;
            font-weight: 400;
            color: $white;
            position: relative;
          }

          .linear {
            top: -20px;
            left: 0;
            position: absolute;
            width: 468px;
            opacity: 1;
            border: 1px dashed $border1;
          }
        }
      }
    }
  }
}
</style>
