<template>
  <div class="sn-page honnr-style" :style="{'background-image': `url(' ${ALI_CLOUD_DOMAIN_STATIC}/hos/dashboard/images/description/gsry_icon_background.png')`}">

    <!-- 左上角标题 -->
    <img class="head" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gsry_icon_title.png'" >

    <!-- 右上角麦穗 -->
    <div class="wheat-box">
      <img class="wheat-left" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gsry_wheat_left.png'" >
      <ul class="sn-f-s-c">
        <li  v-for="(obj, i) in mainList" :key="i" @click="funPreview(obj)">
          <img :src="ALI_CLOUD_DOMAIN_STATIC + obj.url" alt="">
        </li>
      </ul>
      <img class="wheat-right" :src="ALI_CLOUD_DOMAIN_STATIC + '/hos/dashboard/images/description/gsry_wheat_right.png'" >
    </div>

    <!-- 数据展示 -->
    <div class="data-box">
      <!-- 标题 -->
      <div class="title">{{ caption.title }}</div>

      <!-- 列表 -->
      <ul class="sn-f-s-c data-ul">
        <li class="sn-f-col-s-c" v-for="(obj, i) in caption.info" :key="i">
          <p>{{ obj.value }}</p>
          <span>{{ obj.caption }}</span>
        </li>
      </ul>

      <!-- 证书 -->
      <div class="certificate can-scroll">
        <ul class="sn-f-s-c can-scroll animated" :class="{'x-center': slideList.length < 10}">
          <li class="can-scroll" v-for="(obj, i) in slideList" :key="i" @click="funPreview(obj)">
            <img class="can-scroll" :src="ALI_CLOUD_DOMAIN_STATIC + obj.url" />
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>
<script type="text/javascript">
import * as constants from '../../../common/utils/constants';

export default {
  name: 'gsry',

  props: {
    resData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      ALI_CLOUD_DOMAIN_STATIC: constants.ALI_CLOUD_DOMAIN_STATIC, // 静态路径
      caption: {
        title: '',
        info: [
          // {
          //   'caption': '企业资质',
          //   'value': '10+',
          //   'sort': '0',
          //   'remark': ''
          // }, {
          //   'caption': '企业荣誉',
          //   'value': '12+',
          //   'sort': '1',
          //   'remark': ''
          // }, {
          //   'caption': '项目荣誉',
          //   'value': '48+',
          //   'sort': '0',
          //   'remark': ''
          // }
        ]
      },
      mainList: [ // 麦穗证书
        // {
        //   'url': '/hos/dashboard/images/description/gsry_main_1.png',
        //   'sort': '0',
        //   'remark': ''
        // }
      ],
      slideList: [ // 滚动证书
        // {
        //   'url': '/hos/dashboard/images/description/gsry_slide_1.png',
        //   'sort': '0',
        //   'remark': ''
        // }
      ]
    };
  },

  created() {
    this.funLoadData();
  },

  methods: {
    /*
    * 2023-04-14 09:44:49 Ywr
    * 数据初始化
    */
    funLoadData() {
      const res = this.resData;
      // 认证荣誉资质-排序
      res.caption.info = res.caption.info.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });
      this.caption = res.caption;

      let [mainList, slideList] = [res.certificate.main, res.certificate.slide];
      // 麦穗区证书-排序
      this.mainList = mainList.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });

      // 底部轮播证书-排序
      slideList = slideList.sort((a, b) => {
        return (+a.sort) - (+b.sort);
      });

      // 循环后 => 900张无缝轮播
      let endIndex = slideList.length;
      if (slideList.length > 9) {
        for (let i = 0; i < 7; i++) {
          slideList = [...slideList, ...slideList];
        }
        endIndex = 900;
      }
      this.slideList = slideList.slice(0, endIndex);
    },

    /*
    * 2023-04-03 17:56:41 Ywr
    * 预览证书
    */
    funPreview(obj) {
      this.$emit('getPreviewSrc', this.ALI_CLOUD_DOMAIN_STATIC + obj.url);
    }
  }
};
</script>

<style lang="scss">
