  background-repeat: no-repeat;
  position: relative;

  // 左上角头部图片
  .head {
    position: absolute;
    top: 69px;
    left: 50px;
    width: 412px;
    height: 120px;
  }

  // 右上角麦穗
  .wheat-box {
    position: absolute;
    top: 158px;
    right: 144px;

    >img {
      width: 115px;
      height: 202px;
      position: absolute;
      top: 90px;
    }

    .wheat-left {
      left: -71px;
    }

    .wheat-right {
      right: -71px;
    }

    >ul {
      width: fit-content;
      height: fit-content;
      position: relative;
      z-index: 1;

      li {
        width: fit-content;
        height: fit-content;

        &:not(:last-child) {
          margin-right: 12px;
        }

        img {
          width: 288px;
          height: 335px;
        }
      }
    }
  }

  // 介绍
  .data-box {
    width: 100%;
    height: 384px;
    margin-top: 326px;
    background: linear-gradient(124deg, $blue8 0%, $blue14 53%, $blue15 100%);

    @keyframes example {
      from {
        transform: translateX(0);
      }

      to {
        transform: translateX(-50%);
      }
    }

    .animated {
      animation: example 3000s infinite linear;
    }

    // 标题
    .title {
      padding-left: 160px;
      padding-top: 32px;
      margin-bottom: 20px;
      font-size: 48px;
      line-height: 72px;
      font-weight: 400;
      color: $white;
    }

    .data-ul {
      padding-left: 160px;

      li {
        margin-right: 74px;
        color: $white;
        font-size: 18px;
        line-height: 27px;
        font-weight: 400;

        p {
          font-size: 58px;
          line-height: 87px;
          font-weight: 600;
          margin-bottom: 10px;
        }
      }
    }

    // 证书
    .certificate {
      margin-top: 81px;
      width: 100%;
      overflow-x: hidden;
      overflow-y: hidden;
      color: $white;

      &::-webkit-scrollbar {
        display: none;
      }

      ul {
        width: fit-content;

        li {
          margin: 0 5px;
          width: fit-content;

          img {
            width: 199px;
            height: 140px;
          }
        }
      }

      .x-center {
        margin: 0 auto;
      }
    }
  }

  // 预览证书
  // .preview-box {
  //   position: absolute;
  //   z-index: 11;
  //   top: 0;
  //   left: 0;
  //   text-align: center;
  //   width: 100%;
  //   height: 917px;
  //   background: rgba(0, 0, 0, 0.8);
  //   overflow-x: scroll;
  //   overflow-y: hidden;

  //   &::-webkit-scrollbar {
  //     display: none;
  //   }

  //   img {
  //     margin-top: 8px;
  //     height: 900px;
  //     width: fit-content;
  //   }
  // }
}
</style>
